import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { API } from '../../utils/afro-recipe/api';
import { token_name } from '../../utils/afro-recipe/constants';
import { GoogleLogin } from 'react-google-login';

export default class extends Component {

  _isMounted = false;

  constructor(props) {
    super(props)
    this.state = {
      redirectToReferrer: false,
      username: '',
      password: ''
    }
  }
  componentDidMount() {
    this._isMounted = true;
  }

  componentWillUnmount() {
    this._isMounted = false;
  }
  googleSignin = (response) => {
    console.log('hello', response)
  }
  login = (evt) => {
    evt.preventDefault();
    if (this._isMounted) {
      API.localSignIn(this.state.username, this.state.password)
        .then(result => this.setState({ redirectToReferrer: true }))
        .catch(err => localStorage.removeItem(token_name))
        .finally(() => this.setState({ username: '', password: '' }))
    }
  }
  handleChange = (evt) => {
    this.setState({[evt.target.name]: evt.target.value})
  }

  render() {
    let { from } = this.props.location.state || { from: { pathname: '/' } };
    let { redirectToReferrer } = this.state;

    if (redirectToReferrer) return <Redirect to={from} />;

    return (
      <div>
        <div className="logo">
        </div>
        <form onSubmit={this.login}>
          <label>
            <input
              type="text"
              name="username"
              placeholder="Username"
              value={this.state.username}
              onChange={this.handleChange}
              />
          </label>
          <label>
            <input
              type="text"
              name="password"
              placeholder="Password"
              value={this.state.password}
              onChange={this.handleChange}
            />
          </label>
          <button type="submit">Log in</button>
        </form>

        <GoogleLogin
          clientId="940248034518-k2o89ghddupu9hgsfvc3ch1h497gntc5.apps.googleusercontent.com"
          buttonText="Login"
          onSuccess={this.googleSignin}
          onFailure={this.googleSignin}
        />

        <Link to="/signup">sign up</Link>
      </div>
    )
  }
}
