import React, { Component, Fragment } from 'react';
import { TopbarBack } from '../../components';
import { DataContext } from '../../store/DataProvider';
import { recipe_item_storage } from '../../utils/afro-recipe/constants';
import { RecipeComments } from '.';

export default class extends Component {

  state = {
    nav: 'ingredients'
  }

  render() {
    let content;
    switch (this.state.nav) {
      case 'ingredients':
        content = <h1>les ingredients</h1>
        break;
      case 'steps':
        content = <h1>les instructions</h1>
        break;
      case 'comments':
        content = <RecipeComments />
        break;
      default:
        content = <h1>les ingredients</h1>
    }

    return (
      <Fragment>
        <TopbarBack />
        <DataContext.Consumer>
        {context => (
          <section
            style={{
              minHeight: '100vh',
              backgroundAttachment: 'fixed',
              backgroundPosition: 'center',
              backgroundSize: 'cover',
              // backgroundImage: context.state.recipe.mainImageUrl !== null ? `url(${context.state.recipe.mainImageUrl})` : 'url("https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg")'
            }}
          >
            <div 
              className="recipe__details"
              // style={{
              //   position: 'absolute',
              //   background: 'white',
              //   width: '100%',
              //   minHeight: '75vh',
              //   height: 'auto',
              //   top: '25vh'
              // }}
            >
              <h2>{ JSON.parse(localStorage.getItem(recipe_item_storage)).title || context.state.recipe.title }</h2>
              <div>
                <div>
                  <i className="material-icons">home</i>
                  <span>easy</span>
                </div>
                <div>
                  <i className="material-icons">schedule</i>
                  <span>45 min</span>
                </div>
              </div>
            </div>
            <div>
              <nav>
                <ul
                  style={{
                    listStyle: 'none',
                    padding: 0,
                    margin: 0,
                    display: 'flex',
                    justifyContent: 'space-around'
                  }}
                >
                  <li onClick={() => this.setState({ nav: 'ingredients' })}>Ingredients</li>
                  <li onClick={() => this.setState({ nav: 'steps' })}>Steps</li>
                  <li onClick={() => this.setState({ nav: 'comments' })}>commentaires</li>
                </ul>
              </nav>
              <div>
                {content}
                {/* <ul>
                  <li>sel</li>
                  <li>1/2 litre de lait</li>
                  <li>l kg de riz</li>
                  <li>1 1/2 kg de poulet</li>
                </ul> */}
              </div>
            </div>
          </section>
        )}
        </DataContext.Consumer>
      </Fragment>
    )
  }
};