import React from 'react';
import { NavLink } from 'react-router-dom';

export default function (props) {
  return (
    <nav className="bottom-nav">
      <div className="item">
        <NavLink to="/">
          <i className="material-icons">home</i>
          <span>home</span>
        </NavLink>
      </div>
      <div className="item">
        <NavLink to="/cookbook">
          <i className="material-icons">search</i>
          <span>cookbook</span>
        </NavLink>
      </div>
      <div className="item">
        <NavLink to="/login">
          <i className="material-icons">home</i>
          <span>login</span>
        </NavLink>
      </div>
      <div className="item">
        <NavLink to="/signup">
          <i className="material-icons">home</i>
          <span>signup</span>
        </NavLink>
      </div>
      <div className="item">
        <NavLink to="/profile">
          <i className="material-icons">account_circle</i>
          <span>profile</span>
        </NavLink>
      </div>
    </nav>
  )
}