import React, { Component } from "react";
import {API} from "../utils/afro-recipe/api";
import {
  last_recipes_storage,
  last_categories_storage,
  category_item_storage,
  recipe_item_storage,
  user_item_storage } from "../utils/afro-recipe/constants";

export const DataContext = React.createContext();

class DataProvider extends Component {
  state = {
    user: null,
    recipes: [],
    recipe: null,
    recipeOfDay: null,
    categories: [],
    category: null,
    setRecipe: recipe => {
      this.setState({ recipe });
      localStorage.setItem(recipe_item_storage, JSON.stringify(recipe))
    },
    setRecipeOfTheDay: recipes => {
      let recipeDay = recipes[Math.floor(Math.random() * recipes.length)];
      this.setState({ recipeDay })
    },
    setCategory: category => {
      this.setState({ category });
      localStorage.setItem(category_item_storage, JSON.stringify(category))
    },
    setUser: user => {
      this.setState({ user });
      localStorage.setItem(user_item_storage, JSON.stringify(user))
    },
  };

  componentDidMount() {
    API.getRecipes()
      .then(recipes => {
        this.setState({ recipes: recipes.Items })
        this.setState({ recipeDay: Math.floor(Math.random() * recipes.Items) })
        localStorage.setItem(last_recipes_storage, JSON.stringify(recipes))
      })
      .catch(error => console.log('il y a une erreur MyProvider cdm =>', error));

    API.getCategories()
      .then(categories => {
        this.setState({ categories: categories.Items })
        localStorage.setItem(last_categories_storage, JSON.stringify(categories))
      })
      .catch(error => console.log('il y a une erreur MyProvider cdm =>', error))
    
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    console.log('prevProps', prevProps);
    console.log('prevState', prevState);
    console.log('snapshot', snapshot);
  }

  render() {
    return (
      <DataContext.Provider value={{ state: this.state }}>
        {this.props.children}
      </DataContext.Provider>
    )
  }
}

export default DataProvider;