import React, { Component, Fragment } from 'react';
import { Search, BottomNav, RecipeDay } from '../../components';
import { DataContext } from '../../store/DataProvider';
import { API } from '../../utils/afro-recipe/api';
import {NavLink} from "react-router-dom";
import {recipe_item_storage} from "../../utils/afro-recipe/constants";

export default class extends Component {

  state = {
    recipeDay: null,
    isLoaded: false
  };

  onChangeRoute = (data) => {
    this.props.history.push(`/recettes/${data.slug}`);
    // console.log('data =>', data);
  };

  componentDidMount() {
    API.getRecipes()
      .then(data => {
        const recipe = data.Items[Math.floor(Math.random() * data.Items.length)];
        this.setState({ recipeDay: recipe, isLoaded: true })
      })
  }


  render() {
    return (
      <Fragment>
        <Search />
        {/*recipe of the day*/}
        <DataContext.Consumer>
          { context => this.state.recipeDay !== null && this.state.isLoaded ?
            (
              <section
                onClick={() => {
                  this.onChangeRoute(this.state.recipeDay);
                  context.state.setRecipe(this.state.recipeDay)
                }}
              >
                { this.state.recipeDay ?
                  (
                    <Fragment>
                      <h1>{this.state.recipeDay.title}</h1>
                      <p>{this.state.recipeDay.description}</p>
                      <span>{this.state.recipeDay.author.profile.name.display_name}</span>
                    </Fragment>
                  ) : (
                  <h1>pas de recette</h1>
                  )
                }
              </section>
            ) :
            (
              <h1>loading...</h1>
            )
          }
        </DataContext.Consumer>

        {/*{ this.state.isLoaded ? (*/}
          {/*<section*/}
            {/*onClick={() => {*/}
              {/*this.onChangeRoute(this.state.recipeDay)*/}
              {/*localStorage.setItem(recipe_item_storage, JSON.stringify(this.state.recipeDay))*/}
            {/*}}*/}
          {/*>*/}
            {/*<h1>{this.state.recipeDay.title}</h1>*/}
            {/*<p>{this.state.recipeDay.description}</p>*/}
            {/*<span>{this.state.recipeDay.author.profile.name.display_name}</span>*/}
          {/*</section>*/}
        {/*) : (<div>loading...</div>) }*/}

        <h2>Nouveautes</h2>

        <div className="holster">
          <section className="container x mandatory-scroll-snapping" dir="ltr">
            <DataContext.Consumer>
              {context => context.state.recipes.map((r, i) => (
                <div
                  style={{
                    // background: r.image !== {} ? `url(${r.image.secure_url})` : 'url(https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg)'
                  }}
                  key={r.id}
                  onClick={() => {
                    this.onChangeRoute(r)
                    context.state.setRecipe(r)
                  }}
                >
                  <img height="100%" src={r.image.secure_url} alt={r.image.original_filename} />
                  <div className="infos">
                    <h3>{r.title.slice(0,20)}...</h3>
                    {/*<p>{r.description}</p>*/}
                  </div>
                </div>
              ))}
            </DataContext.Consumer>
          </section>
        </div>

        <h2>Top recipes</h2>
        <div className="holster">
          <section className="container x mandatory-scroll-snapping top">
            <DataContext.Consumer>
              {context => context.state.categories.map(c => (
                <div
                  key={c.id}
                  onClick={() => {
                    this.props.history.push('/category/' + c.slug)
                    context.state.setCategory(c)
                  }}
                >
                  <h3>{c.name}</h3>
                  <p>{c.description}</p>
                </div>
              ))}
            </DataContext.Consumer>
          </section>
        </div>

        <BottomNav />
      </Fragment>
    )
  }
};