import React, {Component, Fragment} from 'react';
import {API} from "../../utils/afro-recipe/api";
import { BottomNav } from "../../components";
import { uploadFile } from "../../utils/cloudinary-api";
import {string_to_slug} from "../../utils/slugify";
import { DataContext } from "../../store/DataProvider";

class AddRecipe extends Component {
  constructor() {
    super();
    this.state = {
      countries: [],
      country: '',
      getImage: '',
      preview_img: '',
      title: '',
      description: '',
      category: '',
      cost: 0,
      difficulty: 0,
      fileUrl: '',
      file: '',
      filename: ''
    }
  }

  componentDidMount() {
    fetch('https://restcountries.eu/rest/v2/regionalbloc/au')
      .then(res => res.json())
      .then(countries => this.setState({ countries }))
      .catch(err => console.error(err))
  }


  handleSubmit = (evt) => {
    evt.preventDefault();
    uploadFile(this.state.file)
      .then(result => {
        const data = {
          title: this.state.title,
          slug: string_to_slug(this.state.title),
          description: this.state.description,
          image: {
            resource_type: result.resource_type,
            format: result.format,
            width: result.width,
            height: result.height,
            original_filename: result.original_filename,
            url: result.url,
            secure_url: result.secure_url
          },
          // category: this.state.category,
          cost: Number(this.state.cost),
          difficulty: Number(this.state.difficulty),
          country: {
            cca3: this.state.country
          }
        };
        API.postRecipeInCategory(this.state.category, data)
          .then(data => console.log(data))
          .catch(err => console.log(err))
        // API.addRecipe(data)
        //   .then(res => console.log(res))
        //   .catch(error => console.log(error))
        // console.log(this.state)

        // debugger

      })
      .catch(err => console.log(err))

  };
  handleFile = (evt) => {
    this.setState({ file: evt.target.files[0] });
    const file = evt.target.files[0];
    this.setState({ preview_img: URL.createObjectURL(file) })
    // const reader = new FileReader();
    // reader.onloadend = () => {
    //   this.setState({ preview_img: reader.result });
    //   console.log('result', reader.result)
    // };
    // reader.readAsDataURL(file)
  };

  render() {
    return(
      <Fragment>

        <div>
          <h1>Add your recipe</h1>

          {/*<img width="100%" src={this.state.getImage} alt=""/>*/}

          <form
            style={{
              marginBottom: '65px'
            }}
            onSubmit={this.handleSubmit}
            className="add-recipe"
          >

            <div className="add-recipe__image">
              <label>
                <img width="100%" src={this.state.preview_img} alt=""/>
                <input type="file" onChange={this.handleFile} accept="image/*" />
              </label>
              {/*<S3Album level="public" path="" />*/}
            </div>

            <div className="add-recipe__title">
              <label>
                <input
                  type="text"
                  name="title"
                  placeholder="Write the name of the dish here*"
                  value={this.state.title}
                  onChange={(evt) => this.setState({ [evt.target.name]: evt.target.value})}
                />
              </label>
            </div>

            <div className="add_recipe__description">
              <label>
              <textarea
                name="description"
                placeholder="Write your subtitle or a shortdescription of the dish here."
                value={this.state.description}
                onChange={(evt) => this.setState({ [evt.target.name]: evt.target.value})}
              />
              </label>
            </div>

            <div className="add-recipe__category">
              <select value={this.state.category} onChange={(evt) => this.setState({ category: evt.target.value})}>
                <option value="">-- Please choose a category --</option>

              <DataContext.Consumer>
                { context => context.state.categories.map((c, i) => <option key={i} value={c._id}>{c.title}</option>) }
              </DataContext.Consumer>
              </select>

            </div>

            <div className="add-recipe__difficulty">
              <label>Difficulte</label>
              <input
                type="number"
                name="difficulty"
                onChange={(evt) => this.setState({ difficulty: evt.target.value })}
                value={this.state.difficulty} />
            </div>

            <div className="add-recipe__cost">
              <label>cout</label>
              <input
                type="number"
                name="cost"
                onChange={(evt) => this.setState({ cost: evt.target.value })}
                value={this.state.cost} />
            </div>

            <div className="add-recipe__prep-time"></div>
            <div className="add-recipe__cook-time"></div>
            <div className="add-recipe__wait-time"></div>

            <div className="add-recipe__yield"></div>
            <div className="add-recipe__country">
              <select value={this.state.country} onChange={(evt) => this.setState({ country: evt.target.value})}>
                <option value="">-- Please select a country --</option>
                {this.state.countries.map((country, i) => <option key={i} value={country.alpha3Code}>{country.translations.fr}</option>)}
              </select>
            </div>

            <button type="submit">Save recipe</button>

          </form>
        </div>

        <BottomNav />
      </Fragment>
    )
  }
}

export default AddRecipe;