import React, { Component, Fragment } from 'react';
import { BottomNav } from '../../components';
import { API } from '../../utils/afro-recipe/api';
import { token_name, user_item_storage } from '../../utils/afro-recipe/constants';
// import { DataContext } from '../../store/DataProvider';

export default class extends Component {

  state = {
    isLoaded: false,
    user: {},
  }

  componentDidMount() {
    // const user_id = JSON.parse(localStorage.getItem(token_name)).id;
    API.getCurrentUser()
      .then(user => {
        console.log('user', user[0])
        localStorage.setItem(user_item_storage, JSON.stringify(user));
        this.setState({ user: user[0], isLoaded: true })
      })
      .catch(err => {
        localStorage.removeItem(token_name)
        window.location.replace('/login')
      })
  }

  render() {
    const { isLoaded } = this.state;

    if (!isLoaded) {
      return <div>Loading...</div>
    } else {
      return (
        <Fragment>
          <header
            style={{
              height: '56px',
              width: '100%',
              display: 'flex',
              alignItems: 'center',
              border: '1px solid black'
            }}
          >
            <button className="material-icons">menu</button>
          </header>
          <div
            style={{
              display: 'flex',
              justifyContent: 'center'
            }}
          >
            <div
              style={{
                border: '1px solid black',
                width: '150px',
                height: '150px',
                borderRadius: '50%',
                position: 'relative',
                overflow: 'hidden'
              }}
            >
              <img width="100%" src={this.state.user.profile.image_url} alt="profile image" />
            </div>

            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                flexDirection: 'column'
              }}
            >
              <div>{this.state.user.recipes.length} {this.state.user.recipes > 0 ? 'recettes' : 'recette'}</div>
              <div>56 likes</div>
            </div>

          </div>
          <h1>{this.state.user.profile.name.display_name}</h1>

          <section>
            <h3>mes recettes</h3>
            <div>
              <ul>
                {this.state.user.recipes.map((r, i) => <li key={i}>{r.title}</li>)}
              </ul>
            </div>
          </section>

          <section>
            <h3>mes favories</h3>
            <div>
              <ul>
                {/* {this.state.user.favorite.recipes.map((r, i) => <li key={i}>{r.name}</li>)} */}
              </ul>
            </div>
          </section>

          <BottomNav />
        </Fragment>
      )
    }
  }
};