import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import DataProvider from './store/DataProvider';
// import App from './App';
import { Home } from './pages/Home';
import { Profile } from './pages/Profile';
import { Login, Register, PrivateRoute } from './pages/Auth';
import { RecipeDetail } from './pages/Recipes';
import { CategoryDetails } from './pages/Categories';
import {Cookbook, CookbookDetails} from "./pages/Cookbook";

import './index.css';
import * as serviceWorker from './serviceWorker';

class App extends Component {
  componentDidMount () {
  }
  render() {
    return (
      <Fragment>
        <DataProvider>
          <Switch>
            <Route exact path="/" component={Home} />
            <PrivateRoute exact path="/cookbook" component={Cookbook} />
            <PrivateRoute exact path="/cookbook/:slug" component={CookbookDetails} />
            <Route exact path="/recettes/:slug" component={RecipeDetail} />
            <Route exact path="/category/:slug" component={CategoryDetails} />
            <PrivateRoute exact path="/profile" component={Profile} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/register" component={Register} />
          </Switch>
        </DataProvider>
      </Fragment>
    )
  }
}

ReactDOM.render(
<BrowserRouter><App /></BrowserRouter>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
