import React, { Component, Fragment } from 'react';
import { TopbarBack } from '../../components';
import { DataContext } from '../../store/DataProvider';

export default class extends Component {
  render() {
    return (
      <Fragment>
        <TopbarBack />
        <DataContext.Consumer>
          { context => (
            <section>
              <ul>
                 {context.state.category.recipes.map((r, i) => <li key={i}>{r.title}</li> )}
              </ul>
            </section>
          )}
        </DataContext.Consumer>
      </Fragment>
    )
  }
}