import React, { Component, Fragment } from 'react';
import { Redirect, Link } from 'react-router-dom';
import { API } from '../../utils/afro-recipe/api'
import {token_name} from "../../utils/afro-recipe/constants";
import { DataContext } from '../../store/DataProvider';
import { GoogleLogin } from 'react-google-login';
import FacebookLogin from 'react-facebook-login';

class Login extends Component {
  constructor(props) {
    super(props)
    this.state = {
      redirectToReferrer: false,
      username: '',
      password: ''
    }
  }

  googleSignin = (response) => {
    API.googleLogin(response.accessToken)
      .then(data => {
        // console.log('data', data)
        localStorage.setItem(token_name, JSON.stringify(data))
        this.setState({ redirectToReferrer: true });
      })
      .catch(err => localStorage.removeItem(token_name))
    // console.log('hello', response)
  };
  facebookSignin = (response) => {
    API.facebookLogin(response.accessToken)
      .then(data => {
        // console.log('data', data)
        localStorage.setItem(token_name, JSON.stringify(data))
        this.setState({ redirectToReferrer: true });
      })
      .catch(err => localStorage.removeItem(token_name))
    // console.log('hello', response)
  };
  login = () => {
    // fakeAuth.authenticate(() => {
    //   this.setState({ redirectToReferrer: true });
    // });
  };
  handleSubmit = (evt) => {
    evt.preventDefault();
    API.localSignIn(this.state.username, this.state.password)
      .then(data => {
        localStorage.setItem(token_name, JSON.stringify(data));
        this.setState({ redirectToReferrer: true });
        // console.log(data)
      })
      .catch(err => localStorage.removeItem(token_name))
  };

  render() {
    let { from } = this.props.location.state || { from: { pathname: "/" } };
    let { redirectToReferrer } = this.state;

    if (redirectToReferrer) return <Redirect to={from} />;

    return (
      <Fragment>
        {/*<p>You must log in to view the page at {from.pathname}</p>*/}
        {/*<button onClick={this.login}>Log in</button>*/}
        <DataContext.Consumer>
          { context => (
            <div className="login">

              <div className="login__container">

                <div className="login__container__title">
                  <h1>Sign in</h1>
                </div>

                <form className="login__container__form" onSubmit={this.handleSubmit}>
                  <label>
                    <input
                      type="text"
                      name="username"
                      placeholder="username"
                      autoComplete="username"
                      onChange={(evt) => this.setState({ username: evt.target.value })}
                      value={this.state.username}
                    />
                  </label>
                  <label>
                    <input
                      type="password"
                      name="password"
                      placeholder="password"
                      autoComplete="current-password"
                      onChange={(evt) => this.setState({ password: evt.target.value })}
                      value={this.state.password}
                    />
                  </label>

                  <div className="forgot-password">
                    <span>Forgot password</span>
                  </div>

                  <button type="submit">Done!</button>
                </form>

                <div className="login__container__register">
                  <p>Don't have an account?</p>
                  <Link to="/register">Create a profile</Link>
                </div>

              </div>

            </div>

        )}
        </DataContext.Consumer>

        <GoogleLogin
          clientId="940248034518-k2o89ghddupu9hgsfvc3ch1h497gntc5.apps.googleusercontent.com"
          render={renderProps => (
            <button onClick={renderProps.onClick}>Mon bouton de login</button>
          )}
          buttonText="Login"
          onSuccess={this.googleSignin}
          onFailure={this.googleSignin}
        />

        <FacebookLogin
          appId="1075030476017548"
          autoLoad={false}
          // size="metro"
          fields="name,email,picture"
          onClick={this.facebookSignin}
          callback={this.facebookSignin}
          textButton="mon bouton facebook"
          // render={renderProps => (
          //   <button onClick={renderProps.onClick}>Mon facebook login</button>
          // )}
          cssClass="my-facebook-button-class"
        />

      </Fragment>
    );
  }
}

export default Login;