import { url_endpoint, token_name } from "./constants";

export const API = {
  createUser: async (data) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/oauth/signup`, {
        method: 'POST',
        headers: { 'Content-type': 'application/json' },
        body: JSON.stringify({
          // username: data.username,
          email: data.email,
          password: data.password
        })
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  },
  localSignIn: async (username, password) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/login_check`, {
        method: 'POST',
        headers: { 'content-type': 'application/json' },
        body: JSON.stringify({ username, password })
      });
      return await myFetch.json();
    } catch (e) {
      if (localStorage.getItem(''))
      throw new Error(e);
    }
  },
  googleLogin: async (access_token) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/oauth/google`, {
        method: 'POST',
        headers: { 'content-type': 'application/json' },
        body: JSON.stringify({ access_token })
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  },
  facebookLogin: async (access_token) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/oauth/facebook`, {
        method: 'POST',
        headers: { 'content-type': 'application/json' },
        body: JSON.stringify({ access_token })
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  },
  getCurrentUser: async () => {
    try {
      const myFetch = await fetch(`${url_endpoint}/users/me`, {
        method: 'GET',
        headers: {
          'content-type': 'application/json',
          'Authorization': JSON.parse(localStorage.getItem(token_name)).token
        }
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  },
  getRecipes: async () => {
    try {
      const myFetch = await fetch(`${url_endpoint}/recipes`);
      return await myFetch.json();
    } catch (e) {
      throw new Error(e)
    }
  },
  getCategories: async () => {
    try {
      const myFetch = await fetch(`${url_endpoint}/categories`);
      return await myFetch.json();
    } catch (e) {
      throw new Error(e)
    }
  },
  getRecipeBySlug: async (slug) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/recipes?slug=${slug}.json`);
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  },
  addRecipe: async (data) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/recipes`, {
        method: 'POST',
        headers: {
          'content-type': 'application/json',
          'Authorization': JSON.parse(localStorage.getItem(token_name)).token
        },
        body: JSON.stringify(data)
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e)
    }
  },
  getRecipeOfTheDay: (array) => {
    return Math.floor(Math.random() * array.length);
  },
  postImage: async (data) => {
    try {
      let jsonData = {
        resource_type: data.resource_type,
        format: data.format,
        width: data.width,
        height: data.height,
        original_filename: data.original_filename,
        url: data.url,
        secure_url: data.secure_url,
      };
      const myFetch = await fetch(`${url_endpoint}/images`, {
        method: 'POST',
        headers: {
          'content-type': 'application/json',
          'authorization': 'Bearer ' + JSON.parse(localStorage.getItem(token_name)).token
        },
        body: JSON.stringify(jsonData)
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e)
    }
  },

  postRecipeInCategory: async (category_id, data) => {
    try {
      const myFetch = await fetch(`${url_endpoint}/categories/${category_id}/recipes`, {
        method: 'POST',
        headers: {
          'content-type': 'application/json',
          'authorization': JSON.parse(localStorage.getItem(token_name)).token
        },
        body: JSON.stringify(data)
      });
      return await myFetch.json();
    } catch (e) {
      throw new Error(e);
    }
  }
};