import React, { Component, Fragment } from 'react';
import { DataContext } from '../store/DataProvider';

export default class extends Component {

  like = (context) => {
    console.log('this in like function :', context);
  }
  goBack = () => {
    window.history.back()
  }

  render() {
    return (
      <header className="topbar-back">
        <DataContext.Consumer>
          {context => (
            <Fragment>
                <div>
                  <button
                    onClick={() => this.goBack()}
                    className="material-icons">chevron_left</button>
                </div>
                <div>
                  <button
                    onClick={() => {
                      this.like(context)
                    }}
                    className="material-icons">favorite_border</button>
                  <button className="material-icons">share</button>
                </div>
            </Fragment>
          )}
        </DataContext.Consumer>
      </header>
    )
  }
}